.playpause {
  position: relative;
  background: #191921;
  border: 6px #a3a4a7;
  outline: none;
  height: 140px;
  width: 140px;
  cursor: pointer;
  border-radius: 50%;
  -webkit-animation: pulse 2s ease-in infinite alternate;
          animation: pulse 2s ease-in infinite alternate;
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
  margin-top: 75px;
  margin-bottom: 60px;
}
.playpause.playing,
.playpause:hover {
  -webkit-animation-play-state: running;
          animation-play-state: running;
}
.playpause:focus {
  outline: none;
}
.playpause:before {
  content: '';
  position: absolute;
  top: 50px;
  left: 56px;
  height: 0;
  border-style: solid;
  border-width: 20px 0 20px 40px;
  border-color: transparent transparent transparent #eeeeee;
  transition: 0.2s ease;
}
.playpause.playing:before {
  left: 54px;
  height: 40px;
  border-width: 0 0 0 35px;
}
.playpause.playing:after {
  width: 11px;
}
.playpause:after {
  content: '';
  position: absolute;
  background: inherit;
  width: 0;
  height: 60px;
  top: 30px;
  left: 66px;
  transition: 0.15s ease-in-out;
}
@media screen and (min-width: 768px) {
.audio-visual-music canvas,
  .audio-visual-airport canvas {
    position: absolute;
    left: 50%;
    top: calc(50% - 92px);
    transform: translate(-50%, -50%);
}
.audio-visual-music canvas {
    width: 1400px;
    height: 210px;
}
.audio-visual-airport canvas {
    width: 1000px;
    height: 282px;
}
}
@media screen and (max-width: 768px) {
.audio-visual-airport canvas,
  .audio-visual-music canvas {
    display: none;
}
}
@-webkit-keyframes pulse {
0% {
    box-shadow: 0 0 45px 15px rgba(85, 98, 212, 0.302);
}
50% {
    transform: scale(1);
    box-shadow: 0 0 30px 15px rgba(85, 98, 212, 0.5);
}
100% {
    box-shadow: 0 0 35px 12px rgba(85, 98, 212, 0.4);
}
}
@keyframes pulse {
